.c-layer-popup {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		outline: 0;
		background: rgba(0, 0, 0, 0.6);
		visibility: hidden;
		opacity: 0;
		z-index: -1;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: opacity 0s ease-in-out;
}
.c-layer-popup .buttons {
		justify-content: flex-end;
}
.c-layer-popup .buttons .c-btn {
		min-width: 80px;
}
.c-layer-popup .c-tab-field .c-tab-list > li a {
		background-color: #fff;
}
.c-layer-popup .c-article {
		background-color: #fcfcfc;
}
.c-layer-popup.show {
		visibility: visible;
		z-index: 2000;
		opacity: 1;
}
.c-layer-popup.show .modal-field {
		top: 0;
		opacity: 1;
		transition: all 0.2s ease-in-out;
}

.modal-wrap {
		display: flex;
		align-items: center;
		max-height: calc(100% - 200px);
		flex-direction: column;
		justify-content: center;
		height: 100%;
}
.modal-wrap::before {
		content: none;
}

.modal-field {
		position: relative;
		width: 100%;
		border: 1px solid #e9e9e9;
		box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
		border-radius: 12px;
		background-color: #fff;
		z-index: 2001;
		overflow: hidden;
		color: #494949;
		display: flex;
		flex-direction: column;
		justify-content: center;
		top: -100px;
		opacity: 0;
		transition: all 0.3s ease-out;
		width: 100%;
		max-height: none;
}

.c-layer-popup.type01 .modal-field {
		width: 360px;
}
.c-layer-popup.type01 .c-modal-body {
		margin-top: 32px;
		margin-bottom: 0;
		text-align: center;
}
.c-layer-popup.type01 .c-modal-body .c-modal-inner {
		padding-bottom: 0;
}
.c-layer-popup.type01 .c-modal-footer .inner {
		border-top: none;
		justify-content: center;
}
.c-layer-popup.type01 .c-modal-footer .buttons {
		margin-left: -6px;
		margin-right: -6px;
		justify-content: center;
		width: 100%;
}
.c-layer-popup.type01 .c-modal-footer .buttons > button {
		width: 100%;
		padding-left: 6px;
		padding-right: 6px;
}

.c-layer-popup.type02 .modal-field {
		width: 480px;
}
.c-layer-popup.type02 .c-modal-inner {
		padding-bottom: 20px;
}

.c-layer-popup.type03 .modal-field {
		width: 720px;
}

.c-layer-popup.type04 .modal-field {
		width: 1180px;
}

.c-modal-header {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		position: relative;
		border: 0;
		border-radius: 0;
		flex-shrink: 0;
		padding: 32px 40px 20px;
}
.c-modal-header .c-modal-title-field {
		width: 100%;
}
.c-modal-header .c-modal-title-field .c-title {
		margin-right: 40px;
		text-align: left;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		word-wrap: break-word;
		font-size: 24px;
		font-weight: 700;
		line-height: 1.5;
		color: #253981;
}
.c-modal-header .popup-cls-btn {
		position: absolute;
		top: 32px;
		right: 40px;
		z-index: 10;
		background: url("../images/ico/ico_close_01.svg") center/28px no-repeat;
		width: 28px;
		height: 28px;
}

.c-modal-body {
		margin: 20px 32px 0 40px;
		position: relative;
		overflow-x: hidden;
		overflow-y: auto;
		scrollbar-arrow-color: #e9e9e9;
		scrollbar-track-color: #fff;
		scrollbar-face-color: #e9e9e9;
		scrollbar-3dlight-color: #e9e9e9;
		scrollbar-highlight-color: #e9e9e9;
		scrollbar-shadow-color: #e9e9e9;
}
.c-modal-body::-webkit-scrollbar {
		height: 8px;
		width: 8px;
		background-color: transparent;
}
.c-modal-body ::-webkit-scrollbar-track {
		background-color: transparent;
}
.c-modal-body::-webkit-scrollbar-thumb {
