}
.c-modal-body .c-modal-inner {
		padding-right: 5px;
		padding-bottom: 40px;
}

.c-modal-footer {
		padding: 0 40px 32px;
		flex-shrink: 0;
}
.c-modal-footer .inner {
		align-items: center;
		padding-top: 20px;
		border-top: 1px solid #e9e9e9;
