.sidebar-menu {
		scrollbar-arrow-color: #e9e9e9;
		scrollbar-track-color: #fff;
		scrollbar-face-color: #e9e9e9;
		scrollbar-3dlight-color: #e9e9e9;
		scrollbar-highlight-color: #e9e9e9;
		scrollbar-shadow-color: #e9e9e9;
		height: 100%;
		overflow-y: auto;
}
.sidebar-menu::-webkit-scrollbar {
		height: 8px;
		width: 8px;
		background-color: #fff;
}
.sidebar-menu ::-webkit-scrollbar-track {
		background-color: #fff;
}
.sidebar-menu::-webkit-scrollbar-thumb {
		background: #e9e9e9;
		border-radius: 50px;
		background-clip: padding-box;
}
.sidebar-menu::-webkit-scrollbar-corner {
		background-color: #e9e9e9;
}
.sidebar-menu li a {
		color: rgba(255, 255, 255, 0.8);
		position: relative;
}
.sidebar-menu > li > a {
		padding: 14px 20px;
		color: rgba(255, 255, 255, 0.8);
		display: block;
		font-size: 16px;
		font-weight: 500;
		line-height: 2;
}
.sidebar-menu > li > a:after {
		position: absolute;
		top: 50%;
		right: 10px;
		background: url("../images/ico/ico_arrow_03.svg") right center/100% no-repeat;
		content: "";
		display: inline-block;
		width: 28px;
		height: 28px;
		transform: translateY(-50%);
}
.sidebar-menu > li > a:only-child:after {
		display: none;
}
.sidebar-menu > li > a.on {
		color: #253981;
		background-color: #fff;
}
.sidebar-menu > li > a.on:after {
		background-image: url("../images/ico/ico_arrow_11.svg");
}
.sidebar-menu > li > a.on > .s-menu:before {
		background-position-y: -38px;
}
.sidebar-menu .s-menu {
		position: relative;
		padding-left: 34px;
		vertical-align: middle;
}
.sidebar-menu .s-menu:before {
		display: inline-block;
		position: absolute;
		top: 0;
		left: 0;
		content: "";
		width: 36px;
		height: 28px;
		background-image: url("../images/ico/ico_sprites_01.svg");
		background-position-y: 0;
		background-size: 1092px auto;
}
.sidebar-menu .s-menu.nav01:before {
		background-position-x: -456px;
}
.sidebar-menu .s-menu.nav02:before {
		background-position-x: -494px;
}
.sidebar-menu .s-menu.nav03:before {
		background-position-x: -532px;
}
.sidebar-menu .s-menu.nav04:before {
		background-position-x: -570px;
}
.sidebar-menu .s-menu.nav05:before {
		background-position-x: -608px;
}

.sidebar-sub-menu {
		display: none;
		overflow: hidden;
		padding-top: 12px;
		padding-bottom: 12px;
}
.sidebar-sub-menu > li > a {
		padding: 12px 24px;
		display: block;
		position: relative;
		color: rgba(255, 255, 255, 0.6);
}
