		background-color: #fff;
}

/* 
= Header
*/
.header {
		height: 80px;
		background-color: #fff;
		width: 100%;
		position: fixed;
		padding-left: 260px;
		top: 0;
		z-index: 100;
		border-bottom: 1px solid #e9e9e9;
		left: 0;
}
.header .wrap {
		display: flex;
		padding: 16px 60px;
		justify-content: space-between;
		align-items: center;
		min-width: 550px;
		max-width: 2300px;
}
.header .top-left,
.header .top-right {
		display: flex;
		align-items: center;
}
.header .top-right .user-info {
		display: flex;
		align-items: center;
}
.header .top-right .user-info > li {
		vertical-align: middle;
		padding-left: 10px;
		color: #777776;
}
.header .slogan {
		font-size: 16px;
		font-weight: 500;
		line-height: 2;
		color: #494949;
}
.header .user-profile {
		position: relative;
		border-radius: 20px;
		margin-left: 30px;
		background-color: #253981;
}
.header .user-profile > a {
		opacity: 1;
		padding: 8px 20px 8px 16px;
		transition: opacity 0.3s;
		display: flex;
		align-items: center;
		border-radius: 20px;
		text-align: center;
}
.header .user-profile > a:hover {
		opacity: 0.8;
}
.header .user-profile .name {
		font-size: 16px;
		font-weight: 500;
		line-height: 2;
		color: rgba(255, 255, 255, 0.6);
		padding-left: 16px;
}
.header .user-profile .name > em {
		color: #fff;
}
.header .user-profile .img {
		width: 24px;
		height: 24px;
		border-radius: 50%;
		overflow: hidden;
}
.header .btn-area {
		float: left;
}
.header .btn-area:after {
		display: block;
		content: "";
		clear: both;
}
.header .user-control {
		width: 135px;
		position: absolute;
		z-index: 100;
		top: 49px;
		left: 0;
		border-radius: 8px;
		background-color: #fff;
		box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.25);
		border: 1px solid #e9e9e9;
		display: none;
}
.header .user-control > ul > li ~ li {
		border-top: 1px solid #e9e9e9;
}
.header .user-control > ul > li a,
.header .user-control > ul > li button {
		display: block;
		padding: 11px 20px;
		color: #494949;
		font-size: 14px;
		line-height: 2.1428571429;
}
.header .user-control.on {
		display: block;
}

.contents {
