.spinner-field {
		position: absolute;
		top: 0%;
		left: 0;
		right: 0;
		bottom: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		display: block;
}
.spinner-field > div {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
}

.spinner {
		border: 6px solid #e7e9ea;
		border-radius: 50%;
		width: 58px;
		height: 58px;
		animation: spinner 1.5s linear infinite;
		position: relative;
}
.spinner:after {
		content: "";
		background: url("../images/spinner.svg") 0 0/28px auto no-repeat;
		display: inline-block;
		width: 28px;
		height: 28px;
		position: absolute;
		top: -6px;
		left: 23px;
}

@keyframes spinner {
		0% {
				transform: rotate(0);
		}
		100% {
				transform: rotate(360deg);
		}
}
